<script>
import { hasPermission } from '@/utils/permission.js'

export default {
	data() {
		return {
			userInfo: {}
		}
	},
	
	computed: {
		canManageUser() {
			const userInfo = uni.getStorageSync('userInfo')
			if (!userInfo) return false
			return hasPermission(userInfo.role, 'user.list')
		}
	},
	
	onLoad() {
		this.loadUserInfo()
	},
	
	onShow() {
		// ‰ªéÂÆûÂêçÁºñËæëÈ°µÈù¢ËøîÂõûÊó∂Âà∑Êñ∞
