	},
	
	methods: {
		loadUserInfo() {
			const userInfo = uni.getStorageSync('userInfo')
			if (userInfo) {
				this.userInfo = userInfo
			}
		},
		
		goEditRealName() {
			uni.navigateTo({
				url: '/pages-sub/setting/edit-realname'
			})
		},
		
		goChangePassword() {
